body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: bg-light;
  height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #f8f9fa;
  display: block;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(6px + 2vmin);
  color: black;
}

.App-menu {
  background-color: #f8f9fa;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  margin-top: 0 !important;
}

.App-link {
  color: #61dafb;
}

.my-title {
  font-size: 1.6rem;
}

.card:hover{
  background-color: rgba(108, 117, 125, 0.1);
	border-radius: 25px;	
}

.my-transparentDark{
  background-color: rgba(108, 117, 125, 0.93);
}

.my-flexWrap{
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  display: -webkit-flex;
  display: flex;
}

h2.sidelines { text-align: center; border-bottom: 1px solid #b4b8b8; height: 0.75em; }
h2.sidelines span { display: inline-block; background: #f8f9fa; padding:0 0.5em; }

h2.sidelinesdark { text-align: center; border-bottom: 1px solid rgb(211, 208, 208); height: 0.75em; }
h2.sidelinesdark span { display: inline-block; background: #6c757d; padding:0 0.5em; }


@media screen and (max-width: 991.98px) and (min-width:768px){
  .my-title{
    font-size: 3.2rem;
  }
}

@media screen and (min-width:992px){
  .my-title{
    font-size: 4.8rem;
  }
}

.my-toolbar {  
  position: fixed;
  
  top: 1rem;
  right: 1rem;
  z-index: 2;  
}



.my-navbar {  
  
  position: fixed;
  top: 0rem;
  z-index: 1;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (min-width: 992px){
  .App-header{
    margin-top: 1rem;
  }
}

@-webkit-keyframes slideOutLeft{
  from{
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0,0,0);
  }
  to{
    visibility: hidden;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0,0);
  }
}

@keyframes slideOutLeft{
  from{
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0,0,0);
  }
  to{
    visibility: hidden;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0,0);
  }
}

@-webkit-keyframes slideInLeft{
  from{
    -webkit-transform: translate3d(0%, 0, 0);
    transform: translate3d(0%,0,0);
    visibility: visible;
  }
  to{
    visibility: hidden;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0,0);
  }
}

@keyframes slideInLeft{
  from{
    -webkit-transform: translate3d(0%, 0, 0);
    transform: translate3d(0%,0,0);
    visibility: visible;
  }
  to{
    visibility: hidden;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0,0);
  }
}

.page-enter{
  -webkit-animation: slideInLeft 0.2s forwards;
          animation: slideInLeft 0.2s forwards;  
}

.page-exit{
  -webkit-animation: slideOutLeft 0.2s forwards;
          animation: slideOutLeft 0.2s forwards;  
}

.fade-appear
.fade-enter {
    opacity: 0;
    z-index: 1;
  }
  
  .fade-appear-active
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 3000ms linear 1500ms;
  }

  .fade-exit{
    opacity: 1;
  }

  .fade-exit.fade-exit-active{
    opacity: 0;
    transition: opacity 1500ms linear;
  }

  .my-navButton:active{
    border: 0ch;
    background-color: burlywood;
  }

  .my-bookName{
    width: 8rem; 
    height: 8rem;
    padding: 2rem;
  }

  .my-title{
    font-size: 125%;
  }

  .my-sub-title{
    font-size: 100%;
  }

  .my-fullPage{
    height: 100vh;
  }

  @media screen and (max-width: 666.98px){
    .my-navButton{
      display: none;
    }

    .my-title{
      font-size: 80%;
    }

    .my-sub-title{
      font-size: 60%;
    }
  

    .my-bookName{
      width: 4rem;
      height: 4rem;        
      padding: 0rem;    
    }
  }
  
